footer{
    text-align: center;
    background-color: var(--main-bg-color);
    padding: 10px 0;
    margin: 10px 0;
}

footer > div{
    color: var(--text-color);
}

footer > div > a{
    margin: 10px;
    font-size: 2em;
}

footer > div > a:hover{
    color: var(--decoration-color);
}